.date{
  width: 65%;
  margin: 0 auto;
  font-size: 1.8vw;
}
.choice{
  width: 80%;
  margin: auto;
  font-size: 1.6vw;
  margin-top: 1.3em;
  margin-bottom: 0.7em;
}
.date-edit {
  display:flex;
  width: 80%;
  margin: auto;
  margin-bottom: 2.3vw;
}
.dateTable{
  width: 100%;
  margin-bottom: 2.3vw;
}
input[type="date"] {
  font-size: 1.6vw;
  padding: 5px ;
  margin-right: 10px;
  text-align: center;
}
.dateSubmit {
  font-size: 1.3vw;
}
.date-content {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2vw;
}
.date-content h1{
  font-size: 2.92vw;
  margin: initial;
}
.dateDelete{
  font-size: 1.3vw;
}

@media screen and (min-width: 1367px) {
  .date {
    width: 75%;
  }
  .date-content h1 {
    font-size: 55px;
  }
  .choice {
    font-size: 22px;
  }
  input[type="date"] {
    font-size: 20px;
  }
  .dateDelete {
    font-size: 20px;
  }
  .dateSubmit {
    font-size: 18px;
  }
}

@media screen and (max-width: 768px) {
  .Tabs {
    width: 80%;
  }
}

@media screen and (max-width: 480px) {
  .date {
    width: 88%;
  }
  .dateSubmit {
    font-size: 14px;
    padding: 0 0.6em;
  }
  .choice {
    width: 100%;
    font-size: 16px;
  }
  input[type=date] {
    font-size: 16px;
    width: 160px;
  }
 .dateDelete {
    font-size: 14px;
    padding: 0.2em 0.6em;
  }
  .date-edit {
    width: 100%;
    margin-bottom: 16px;
  }
  .dateTable {
    margin-bottom: 16px;
  }
  .date-content h1 {
    font-size: 27px;
  }
  .Tabs {
    width: 88%;
  }
  .TabList {
    display: flex;
    flex-wrap: wrap;
    justify-content: left !important;
  }
  .TabList li {
    width: 24.5% !important;
    padding: 2vw 0 !important;
    font-size: 16px !important;
  }
}