.memo li {
  display: flex;
  align-items: center;
}
.list ul{
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  width: 100%;
}
.list{
  font-size: 1.5vw;
  padding-left: 0.8em;
}
.MuiTableRow-root {
  display: block !important;
  width: 78%;
}

@media screen and (max-width: 480px) {
  .list {
    font-size: 20px;
    padding-left: 0.2em;
  }
  .MuiTableRow-root {
    width: 95%;
  }
  .List .MuiTableCell-root {
    padding-bottom: 10px !important;
    margin-bottom: 10px !important;
    gap: 15px !important;
  }
}
