.PanelItem {
  display: block;
  width: calc(100% / 2);
  padding: 2vw;
  height: 33vw;
  border: 1px solid #aaa;
  border-top: none;
  overflow: scroll;
}
.PanelItem:first-child{
  border-right: none;
}
.Panel_title{
  font-size: 1.5vw;
  margin: initial;
  margin-bottom: 1em;
}

@media screen and (max-width: 480px) {
  .Panel_title {
    font-size: 18px;
    margin-bottom: 0.7em;
  }
  .PanelItem {
    height: 70vw;
    width: 100% !important;
    padding: 17px !important;
  }
  .PanelItem:first-child {
    border-right: 1px solid #aaa;
  }
}
