.formContainer {
  position: relative;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 90vh;
}
.formContainer h1 {
  text-align: center;
  font-size: 2.2vw;
  margin: initial;
}
.formContainer form {
  position: absolute;
  content: '';
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 40%;
  max-width: 450px;
  height: fit-content;
  border: 1px solid #dfefef;
  padding: 2.2vw;
  border-radius: 10px;
  background-color: #f7f4f4;
  box-shadow: 2px 12px 17px -7px #383838;
  border-radius: 15px;
}
.uiForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-top: 2vw;
}
.uiForm p{
  font-size: 1.2vw;
  margin: 0;
}
.uiForm a{
  font-size: 1.5vw;
  text-decoration: none;
  color: #000;
  border-bottom: 1px solid #000;
}
.formField {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin-bottom: 2vw;
}
.formField input {
  font-size: 1.4vw;
  border: 1px solid grey;
  padding: 1.2vw 1.4vw;
  border-radius: 4px;
}
.formField input:focus {
  outline: none;
}
.formField label {
  font-size: 1.4vw;
  font-weight: 600;
  margin-bottom: 0.5em;
}
.submitButton {
  background: #0563b4;
  width: 100%;
  border: none;
  border-radius: 5px;
  color: white;
  margin-top: 2vw;
  margin-bottom: 1vw;
  padding: 1vw 0;
  font-size: 1.6vw;
  cursor: pointer;
  transition: all 0.3s;
}
.submitButton:hover {
  background-color: #0e528d;
}
.errorMsg {
  color: red;
  margin: 0;
  align-self: flex-start;
}
.msgOk {
  color: green;
  margin-top: 15px;
}

@media screen and (min-width: 1366px) {
  .formContainer form {
    width: 450px;
    padding: 30px;
  }
  .formContainer h1 {
    text-align: center;
    font-size: 36px;
  }
  .formField {
    margin-bottom: 32px;
  }
  .formField input {
    font-size: 20px;
    padding: 20px 16px;
  }
  .formField label {
    font-size: 22px;
  }
  .submitButton {
    font-size: 22px;
    margin-top: 20px;
    margin-bottom: 16px;
  }
  .uiForm {
    margin-top: 24px;
  }
  .uiForm p {
    font-size: 20px;
  }
  .uiForm p a{
    font-size: 23px;
  }
}

@media screen and (max-width: 768px) {
  .formContainer form {
    width: 47%;
    max-width: initial;
  }
  .formContainer h1 {
    font-size: 2.8vw;
    margin-bottom: 0.5em;
  }
  .formField {
    margin-bottom: 3vw;
  }
  .formField label {
    font-size: 2vw;
  }
  .formField input {
    font-size: 1.8vw;
  }
  .submitButton {
    font-size: 2.2vw;
  }
  .uiForm p {
    font-size: 2vw;
  }
  .uiForm p a{
    font-size: 2.3vw;
  }
}

@media screen and (max-width: 480px) {
  .formContainer h1 {
    text-align: center;
    margin: 13px 0;
    font-size: 20px;
  }
  .formContainer form {
    position: absolute;
    content: '';
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 80%;
    height: fit-content;
    max-width: 450px;
    padding: 22px ;
    border-radius: 10px;
    box-shadow: 2px 12px 17px -7px #383838;
    border-radius: 15px;
  }
  .uiForm {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    height: 290px;
    margin-top: 15px;
  }
  .uiForm p{
    font-size: 16px;
    padding: 0;
  }
  .uiForm p a{
    font-size: 18px;
  }
  .formField {
    display: flex;
    flex-direction: column;
    width: 100%;
  }
  .formField input {
    font-size: 17px;
    border: 1px solid grey;
    padding: 10px 12px;
    border-radius: 4px;
  }
  .formField input:focus {
    outline: none;
  }
  .formField label {
    font-size: 15px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  .submitButton {
    margin-top: 3px;
    padding: 10px 30px;
    font-size: 15px;
  }
  .submitButton:hover {
    background-color: #0e528d;
  }
  .errorMsg {
    color: red;
    margin: 0;
    align-self: flex-start;
  }
  .msgOk {
    color: green;
    margin-top: 15px;
  }
}