.Tabs{
  width: 80%;
  max-width: 1200px;
  margin: 0 auto;
  margin-bottom: 4vw;
}
.TabList {
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #aaa;
  margin: initial;
  padding: initial;
}
.TabList li{
  text-align: center;
  width: calc(100% / 8);
  font-size: 1.5vw;
  padding: 1vw 0;
}
.TabPanel {
  border-top: none;
}

.TabItem{
  display: flex;
}

@media screen and (min-width: 1367px) {
  .TabList li{
    font-size: 24px;
  }
}

@media screen and (max-width: 768px) {
  .Tabs {
    width: 80%;
  }
}

@media screen and (max-width: 480px) {
  .Tabs {
    width: 88%;
    padding-bottom: 50px;
  }
  .TabItem {
    display: block;
  }
}