.Form{
  display: flex;
  align-items: center;
  justify-content: left;
  border: none !important;
  width: fit-content;
}
.Form .MuiTableCell-root {
  border: none !important;
  padding-bottom: 1vw !important;
}
.Form .submit {
  margin-left: 1vw;
}
.inputForm {
  font-size: 1.4vw;
  padding: 0.3em 0.5em;
}
.submit{
  font-size: 1.3vw;
  padding: 0.2em 0.6em;
  cursor: pointer;
}
.MuiTableCell-root {
  display: flex !important;
  align-items: center;
  justify-content: space-between;
  padding: 1.2vw 0 !important;
  padding-top: 0 !important;
  margin-bottom: 1.2vw !important;
  font-size: 1.3vw !important;
  text-align: left;
}
.MuiButton-outlined {
  padding: 8px 0.8vw !important;
  margin-left: 2vw;
}
.MuiButton-root {
  min-width: 45px !important;
  padding: 0.5vw 1vw !important;
} 

@media screen and (max-width: 480px) {
  .Form {
   width: 100%;
  }
  .Form .inputForm {
    font-size: 16px;
  }
  .Form .submit {
    margin-left: 12px;
    font-size: 15px;
  }
  .Form .MuiTableCell-root {
    padding-bottom: 12px !important;
  }
  .MuiButton-root {
    padding: 5px !important;
  }
}


