.Header a{
  color: rgba(0, 0, 0, 0.788);
}
.Header .site-header{
  background: rgba(208, 208, 208, 0.788);
  width: 100%;
  padding: 1.1vw 0;
  z-index: 5;
}
.Header .site-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  max-width: 1500px;
  height: 100%;
  margin: auto;
}
.Header .brand {
  width: 100%;
  font-size: 2.6vw;
  font-weight: bold;
  color: #000;
  text-decoration: none;
  font-family: 'Noto Sans JP', 'ヒラギノ角ゴ Pro W3', 'Hiragino Kaku Gothic Pro','メイリオ', 'Meiryo', '游ゴシック', 'Yu Gothic', 'ＭＳ Ｐゴシック', 'MS PGothic', sans-serif;
  letter-spacing: 0.01em;
}
.Header .nav { 
  text-align: right;
  width: 100%;
  height: 100%;
}
.Header .nav__toggle{
  display: none;
}
.Header .nav__wrapper{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 2vw;
}
.Header .nav__item {
  display: inline-block;
}
.Header .nav__item a{
  font-size: 1.5vw;
  padding: 0;
  text-decoration: none;
  cursor: pointer;
}
.Header .nav__item a:hover {
  border-bottom: 1px solid #000;
}
.Header .signOut {
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  appearance: none;
  font-size: 1.5vw;
}
.Header .cp_cont{
  display: none;
  left: -100%;
}
.LoginHeader .site-header {
  padding: 1.5vw 0;
}

@media screen and (min-width: 1367px) {
  .Header .site-header {
    padding: 15px 0;
  }
  .Header .brand {
    font-size: 33px;
  }
  .Header .nav__item a {
    font-size: 18px;
  }
  .Header .signOut {
    font-size: 18px;
  }
  .LoginHeader .site-header {
    padding: 21px 0;
  }
}

@media screen and (max-width: 768px) {
  .Header .site-header {
    height: 70px;
  }
  .Header .nav {
    display: none;
  }
  .Header .nav__wrapper{
    display: none;
  }
  .Header .nav__toggle{
    display: inline-block;
  }
  .Header .cp_cont{
    display: block;
    left: -100%;
  }
  .Header .signOut{
    display: block;
    padding: 20px;
    font-size: 1.8vw;
    width: 100%;
    text-align: left;
    text-decoration: none;
    color: #ffffff;
    border-bottom: 1px solid #ffffff;
  }
}

@media screen and (max-width: 480px) {
  .Header .site-header {
    height: 60px;
  }
  .Header .brand {
    font-size: 20px;
  }
  .Header .signOut {
    text-align: center;
    font-size: 18px;
    width: 90%;
    margin: auto;
  }
}
