.loading{
  position: absolute;
  content: '';
  text-align: center;
  font-size: 3.2vw;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: fit-content;
}

@media screen and (min-width: 321px) and (max-width: 450px){
  .loading{
    font-size: 2rem;
    margin-top: 23rem;
  }
}
