.Contact {
  text-align: center;
  padding-bottom: 5vw;
}
.Contact .title{
  font-size: 2.92vw;
  margin-top: 7.32vw;
  margin-bottom: 2vw;
}
.Contact .contactForm{
  width: 100%;
  margin: 0 auto;
}
.Contact .contact-item {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3vw;
  padding-bottom: 1.9vw;
  margin-bottom: 1.68vw;
}
.Contact .contact-item .contact-left{
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 20.83vw;
  font-size: 1.6vw;
  font-family: yugothic_b;
}
.Contact .contact-item .contact-left p {
  margin: initial;
}
.Contact .contact-item.no_center {
  align-items: flex-start;
}
.Contact .contact-item.no_center .contact-left {
 padding-top: 1em;
}
.Contact .contact-item input,  .contact-item select{
  width: 94%;
  border-radius: 6px;
  background-color: #f3f3f3;
  border: none;
  padding: 1em 0.7em;
  font-size: 1.6vw;
}
.Contact .contact-item .contact-right{
  width: 44.2vw;
  font-size: 1.4vw;
}
.Contact .contact-item .contact-right p{
  font-size: 1.5vw;
  color: #000;
  white-space: nowrap;
  margin-left: 0.3em;
  line-height: 1.2;
}
.Contact .contact-item .contact-left .requierd {
  font-size: 1.2vw;
  color: #fff;
  background-color: #e60012;
  padding: 0.4em 0.5em;
  border-radius: 3em;
  font-family: yugothic_b;
}
.Contact .contact-item textarea {
  width: 94%;
  height: 14.64vw;
  border-radius: 3px;
  background-color: #f3f3f3;
  border: none;
  padding: 2.681% 2.5%;
  font-size: 1.7vw;
}
.Contact .contactSubmit{
  display: block;
  cursor: pointer;
  font-size: 1.3vw;
  letter-spacing: 0.3em;
  width: 20%;
  border-radius: 5px;
  margin: 30px auto;
  padding: 7px 20px;
  color: #fff;
  background-color: #17a2b8;
  border-color: #17a2b8;
}
.Contact .contact-submit-message{
  font-size: 2rem;
  margin: 100px;
}
.Contact .link {
  font-size: 1.4vw;
}

@media screen and (max-width: 767px) {
  .Contact .contact-item .contact-left {
    font-size: 2vw;
    width: 24.83vw;
  }
  .Contact.contact-item .contact-left span.requierd {
    font-size: 1.5vw;
  }
}

@media screen and (max-width: 480px) {
  .Contact {
    padding-bottom: 40px;
  }
  .Contact .title {
    font-size: 30px;
    margin-top: 30px;
    margin-bottom: 20px;
  }
 .Contact .contact-item{
    display: block;
    width: 80%;
    margin: auto;
    padding-bottom: 26px;
  }
  .Contact .contact-item .contact-left {
    margin: 0;
    font-size: 14px;
    font-size: 2vw;
    width: 60%;
  }
  .Contact .contact-item .contact-left p {
    margin: initial;
    font-size: 16px;
  }
  .Contact .contact-item .contact-left .requierd {
    font-size: 12px;
    padding: 0.2em 0.5em;
  }
  .Contact .contact-item .contact-left span.requierd {
    font-size: 11px;
  }
  .Contact .contact-item .contact-right {
    text-align: left;
    width: 100%;
  }
  .Contact .contact-item input[type='text'], 
  .Contact .contact-item input[type='email'], 
  .Contact .contact-item input[type='tel'] {
    width: 100%;
    font-size: 16px;
    padding: 10px;
    margin-top: 10px;
  }
  .Contact .contact-item textarea {
    width: 100%;
    font-size: 16px;
    padding: 10px;
    margin-top: 10px;
    height: 190px;
  }
  .Contact .form-button input {
    height: 40px;
    line-height: 39.8px;
    font-size: 16px;
    margin-top: 20px;
  }
  .contactSubmit {
    font-size: 16px;
    letter-spacing: 0.3em;
    width: 45%;
    border-radius: 5px;
    margin: 20px auto;
    margin-top: 0;
    padding: 7px 20px;
  }
  .Contact .link {
    font-size: 14px;
  }
}


